.header {
  position: absolute;
  // top: 1rem;
  top: -100%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;

  @media screen and (max-width: 23.75em /* 380px */) {
    left: -1rem;
    transform: none;
  }

  &__wrapper {
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60vw;

    @media screen and (max-width: 75em /* 1200px */) {
      width: 70vw;
    }

    @media screen and (max-width: 64.375em /* 1030px */) {
      width: 85vw;
    }

    @media screen and (max-width: 53.125em /* 850px */) {
      width: 95vw;
    }

    @media screen and (max-width: 23.75em /* 380px */) {
      width: 100vw;
    }
  }

  &__left {
    display: flex;
    align-items: center;
    gap: 4rem;
  }

  &__list {
    list-style: none;
    font-size: 1.8rem;
    font-weight: 700;
  }

  @media screen and (max-width: 42.1875em /* 675px */) {
    &__item {
      &--responsive-hide {
        display: none;
      }
    }
  }

  &__link {
    &:link,
    &:visited {
      color: inherit;
      text-decoration: none;
    }
  }
}
