.projects {
  padding: 4rem;

  @media screen and (min-width: 112.5em /* 1800px */) {
    padding: 15vw;
  }

  @media screen and (max-width: 112.5em /* 1800px */) {
    padding: 15vw;
  }

  @media screen and (max-width: 100em /* 1600px */) {
    padding: 15vw;
  }

  @media screen and (max-width: 84.375em /* 1350px */) {
    padding: 3rem;
  }

  @media screen and (max-width: 46.875em /* 750px */) {
    padding: 1rem;
  }

  &__heading {
    font-size: 5rem;
    margin-bottom: 5rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: -3px;

    @media screen and (max-width: 36.25em /* 580px */) {
      font-size: 3rem;
      letter-spacing: -1px;
    }
  }

  &__header {
    font-size: 6rem;
    font-style: italic;
    padding-bottom: 1rem;
  }
}

.project {
  margin-top: 6rem;

  &__header {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;

    span {
      grid-column: 2 / span 2;
      border-top: 1px solid #333;
      padding: 1rem;
      font-size: 3rem;
      text-align: center;

      @media screen and (max-width: 36.25em /* 580px */) {
        font-size: 2rem;
      }
    }
  }

  &__details {
    margin-top: 2rem;
  }

  &__heading {
    position: relative;
    font-size: 3vw;
    display: flex;
    align-items: center;

    @media screen and (max-width: 28.125em /* 450px */) {
      font-size: 5vw;
    }
  }

  &__image {
    position: relative;
    width: 100vw;
    max-height: 70rem;

    @media screen and (max-width: 36.25em /* 580px */) {
      max-height: 60rem;
    }

    img {
      position: absolute;
      right: 10vw;
      top: 4.8rem;
      width: 60vw;
      object-fit: cover;
      object-position: left top;

      @media screen and (min-width: 112.5em /* 1800px */) {
        width: 30vw;
        right: 25vw;
        height: 25rem;
      }

      @media screen and (max-width: 112.5em /* 1800px */) {
        width: 30vw;
        right: 25vw;
        height: 25rem;
      }

      @media screen and (max-width: 100em /* 1600px */) {
        width: 40vw;
        height: 25rem;
        right: 20vw;
      }

      @media screen and (max-width: 84.375em /* 1350px */) {
        right: 5rem;
        width: 50vw;
        height: 30rem;
      }

      @media screen and (max-width: 65.625em /* 1050px */) {
        width: 50vw;
        right: 7rem;
      }

      @media screen and (max-width: 46.875em /* 750px */) {
        right: 2rem;
        height: 15rem;
        object-position: left;
      }

      @media screen and (max-width: 30em /* 480px */) {
        width: 45%;
        top: 3.2rem;
        right: 3rem;
      }
    }
  }

  &__links {
    padding-top: 3rem;

    .button {
      color: var(--color-white);

      &::before {
        background: var(--color-black);
      }

      &::after {
        border-color: var(--color-black);
      }
    }
  }
}

.all-projects {
  margin-top: 7.4rem;
  display: grid;
  place-items: center;

  &__link {
    &:link,
    &:visited {
      font-size: 1.6rem;
      text-decoration: none;
      text-transform: uppercase;
      padding: 2rem 4rem;
      color: var(--color-white);
      background: no-repeat
        linear-gradient(
          105deg,
          var(--color-purple-light) 50%,
          var(--color-black) 50%
        );
      background-size: 200%;
      background-position: 100%;
      border-radius: 50vh;
      transition: background-position 500ms;
    }

    &:hover {
      background-position: 0;
    }
  }
}
