.link {
  &:link,
  &:visited {
    color: inherit;
    text-decoration: none;
    position: relative;
    display: grid;
    place-items: center;
    overflow: hidden;

    span {
      transition: transform 300ms cubic-bezier(0.5, 0.5, 0.3, 1);
    }
  }

  &:hover {
    span {
      transform: translate3d(-150%, 0, 0);
    }

    &::before {
      transform: scale3d(1, 1, 1); // more performant that scale
      transform-origin: left;
    }

    &::after {
      transform: translate3d(0, 0, 0); // more performant that translate
    }
  }

  &::before,
  &::after {
    position: absolute;
    transition: transform 300ms cubic-bezier(0.5, 0.5, 0.3, 1);
  }

  &::before {
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: currentColor;
    transform: scale3d(0, 1, 1); // more performant that scale
    transform-origin: right;
  }

  &::after {
    content: attr(data-text);
    top: 0;
    height: 100%;
    transform: translate3d(150%, 0, 0); // more performant that translate
  }

  @media screen and (max-width: 23.75em /* 380px */) {
    &[data-text="Home"] {
      display: none;
    }
  }
}
