@keyframes pulse {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes width-animation {
  0% {
    width: 85%;
  }

  100% {
    width: 28rem;
  }
}

@keyframes icon-animation {
  0% {
    filter: drop-shadow(0.5rem 0.4rem var(--color-red))
      drop-shadow(-0.5rem -0.6rem var(--color-blue-light));
  }

  25% {
    filter: drop-shadow(-0.5rem -0.6rem var(--color-red))
      drop-shadow(0.5rem 0.4rem var(--color-blue-light));
  }

  50% {
    filter: drop-shadow(0.5rem -0.4rem var(--color-red))
      drop-shadow(-0.8rem 0.4rem var(--color-blue-light));
  }

  75% {
    filter: drop-shadow(-0.8rem -0.4rem var(--color-red))
      drop-shadow(-0.5rem -0.4rem var(--color-blue-light));
  }

  100% {
    filter: drop-shadow(-0.5rem 0 var(--color-red))
      drop-shadow(0.5rem -0.4rem var(--color-blue-light));
  }
}

@keyframes scroll-wheel {
  0% {
    top: 1px;
  }

  25% {
    top: 2px;
  }

  50% {
    top: 3px;
  }

  75% {
    top: 2px;
  }

  100% {
    top: 1px;
  }
}

@keyframes scroll-arrow {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes move-left {
  0% {
    transform: translate3d(0, 0, 0);
  }

  50% {
    transform: translate3d(-50%, 0, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes move-right {
  0% {
    transform: translate3d(0, 0, 0);
  }

  50% {
    transform: translate3d(+50%, 0, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes rotate {
  to {
    transform: rotate(-360deg);
  }
}

@keyframes hide {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    opacity: 1;
  }

  10% {
    opacity: 0;
  }
}
