.introduction {
  position: absolute;
  top: 20%;
  // left: 2rem;
  left: -100%;
  width: 100%;
  height: 60%;
  z-index: 2;
  color: var(--color-white);

  &__heading {
    font-size: 3.3rem;

    @media screen and (max-width: 21.875em /* 350px */) {
      font-size: 2.7rem;
    }

    @media screen and (max-width: 18.75em /* 300px */) {
      font-size: 2.3rem;
    }

    span {
      font-size: 3.6rem;
      width: 93%;
      padding-top: 1.6rem;

      @media screen and (max-width: 21.875em /* 350px */) {
        font-size: 3rem;
      }

      @media screen and (max-width: 18.75em /* 300px */) {
        font-size: 2.7rem;
      }
    }
  }
}
