.circle-eye {
  position: relative;
  display: inline-block;

  @media screen and (max-width: 84.375em /* 1350px */) {
    transform: translate(-14rem, -3rem);
  }

  @media screen and (max-width: 20em /* 320px */) {
    transform: translate(0, 0);
  }

  .textcircle {
    display: block;
    width: 25rem;
    animation: rotate 5s linear infinite;

    @media screen and (max-width: 46.875em /* 750px */) {
      width: 20rem;
    }

    @media screen and (max-width: 36.25em /* 580px */) {
      width: 12rem;
    }

    text {
      font-size: 3.2rem;
      letter-spacing: 1rem;
      text-transform: uppercase;
      fill: var(--color-black);
    }
  }

  &:hover {
    .eye__inner,
    .eye__iris,
    .eye__lashes--up {
      animation: hide 2s step-end infinite;
    }

    .eye__lashes--down {
      animation: show 2s step-end infinite;
    }
  }
}

.eye {
  position: absolute;
  width: 6rem;
  height: 6rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &__outer,
  &__inner,
  &__lashes--up,
  &__lashes--down {
    stroke: var(--color-black);
    stroke-width: 1.5px;
    fill: none;
  }

  &__lashes--down {
    opacity: 0;
  }
}
