.skills {
  position: relative;
  font-size: 3.3rem;
  height: 20rem;
  background: var(--color-yellow);
  overflow: hidden;
  text-align: center;
  padding-top: 1.6rem;

  @media screen and (max-width: 43.75em /* 700px */) {
    font-size: 2.5rem;
  }

  @media screen and (max-width: 31.25em /* 500px */) {
    height: 15rem;
    font-size: 1.8rem;
  }

  &__header {
    line-height: 1;
    font-size: 4rem;
    font-weight: 600;

    @media screen and (max-width: 43.75em /* 700px */) {
      font-size: 3.5rem;
    }

    @media screen and (max-width: 31.25em /* 500px */) {
      font-size: 3rem;
    }
  }

  &__text {
    position: absolute;
    white-space: nowrap;
    display: flex;
    align-items: center;
    gap: 6rem;

    @media screen and (max-width: 43.75em /* 700px */) {
      gap: 4rem;
    }

    @media screen and (max-width: 31.25em /* 500px */) {
      gap: 3rem;
    }

    &--move-left {
      left: 0;
      top: 37.5%;
      animation: move-left 30s linear infinite;
    }

    &--move-right {
      right: 0;
      top: 63.5%;
      animation: move-right 30s linear infinite;
    }
  }
}

.skill {
  transition: transform 200ms;
  cursor: pointer;

  &:hover {
    font-weight: 600;
    font-size: 97.5%;
    transform: skew(-20deg);
  }
}
