.socials {
  position: absolute;
  left: 50%;
  bottom: 20rem;
  transform: translate3d(-50%, 0, 0);
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  @media screen and (max-width: 28.125em /* 450px */) {
    width: 75vw;
    gap: 1rem;
  }

  align-items: center;
  gap: 3rem;
  z-index: 2;
  background: rgba(0, 0, 0, 0.5);
  padding: 2rem 4rem;
  border-radius: 50vh;

  &__link {
    color: var(--color-white);
    font-size: 2rem;
    text-decoration: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 1rem;

    @media screen and (max-width: 48.125em /* 770px */) {
      font-size: 1.4rem;
      gap: 0.5rem;
    }

    @media screen and (max-width: 35.625em /* 570px */) {
      flex-direction: column;
    }

    @media screen and (max-width: 32.5em /* 520px */) {
      font-size: 1.1rem;
    }

    &:first-child {
      grid-column: 1 / -1;
    }

    &:hover {
      animation: icon-animation 200ms infinite;
    }
  }

  &__icon {
    fill: var(--color-white);
    height: 3.5rem;
    width: 3.5rem;

    @media screen and (max-width: 35.625em /* 570px */) {
      height: 2.5rem;
      width: 2.5rem;
    }
  }
}
