.button {
  text-decoration: none;
  position: relative;
  border: none;
  cursor: pointer;
  width: 17.5rem;
  height: 12rem;
  background: none;
  color: var(--color-white);
  font-size: 1.8rem;
  display: inline-grid;
  place-items: center;

  @media screen and (max-width: 50em /* 800px */) {
    transform: scale3d(0.7, 0.7, 0.7);
    font-size: 2.3rem;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &::before {
    background: var(--color-white);
    clip-path: path(
      "M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z"
    );
    transition: clip-path 500ms cubic-bezier(0.585, 2.5, 0.645, 0.55);
  }

  &::after {
    width: 95%;
    height: 85%;
    top: 0.6rem;
    border: 1px solid var(--color-white);
    border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
    transform: rotate(-20deg);
    transition: transform 500ms cubic-bezier(0.585, 2.5, 0.645, 0.55);
  }

  &:hover {
    &::before {
      clip-path: path(
        "M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z"
      );
    }

    &::after {
      transform: translate3d(0, -0.5rem, 0);
    }
  }

  & span {
    mix-blend-mode: difference;
    font-weight: 500;
  }
}
