.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
  z-index: 99;
  background: var(--color-blue-dark);
  display: grid;
  place-items: center;
  font-family: "Orbitron", sans-serif;

  &__box {
    position: relative;
    width: 50rem;
    height: 20rem;
    border: 3px solid var(--color-green);
    border-top: 3px solid var(--color-green-dark);
    border-bottom: 3px solid var(--color-green-dark);

    @media screen and (max-width: 37.5em /* 600px */) {
      width: 90vw;
    }
  }

  &__text {
    position: relative;
    color: var(--color-white);
    padding: 1rem;
    font-size: 2rem;
    font-weight: 700;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 21.875em /* 350px */) {
      font-size: 1.7rem;
    }

    &--dot {
      width: 1.5rem;
      height: 1.5rem;
      margin: 0 3px;
      border-radius: 50%;
      background: var(--color-green);
      animation: pulse 1s infinite alternate;
    }

    &--border {
      width: 85%;
      height: 1px;
      background: var(--color-green-dark);
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__bar {
    width: 90%;
    height: 1rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #ccc;
    border-radius: 2px;

    &--inner {
      height: 100%;
      width: 0;
      border-radius: 2px;
      background: var(--color-green);
    }
  }

  &__counter {
    position: absolute;
    top: 70%;
    left: 0;
    color: var(--color-white);
    font-size: 2rem;
    font-weight: 700;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;

    &--number {
      color: var(--color-green);
    }
  }

  &__width-animation {
    animation: width-animation 1s forwards;
  }

  &__svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 85%;
    opacity: 0;
  }
}
