.testimonials {
  padding: 3.2rem;
  background: var(--color-blue-dark-2);

  &__heading {
    color: var(--color-white);
    padding: 5rem;
    font-size: 4rem;

    @media screen and (max-width: 46.875em /* 750px */) {
      font-size: 3rem;
    }

    @media screen and (max-width: 25rem /* 400px */) {
      font-size: 2.4rem;
    }
  }
}

.swiper {
  min-height: 50rem;
  user-select: none;

  @media screen and (max-width: 25rem /* 400px */) {
    min-height: 40rem;
  }
}

.testimonial {
  position: relative;
  display: flex;
  font-size: 1.6rem;
  padding: 0.5rem;
  color: var(--color-white);

  &__card {
    position: relative;
    width: 100%;
    padding: 2.4rem 0;
    display: flex;
    gap: 1.6rem;
  }

  &__icon {
    width: 4.8rem;
    height: 4.8rem;
    fill: var(--color-red);
  }

  &__top-border {
    background: var(--color-white);
    width: 100%;
    height: 1px;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__quote {
    @media screen and (max-width: 25rem /* 400px */) {
      font-size: 1.4rem;
    }

    @media screen and (max-width: 21.875rem /* 350px */) {
      font-size: 1.2rem;
    }

    span {
      line-height: 0.9;
      display: inline-block;
      margin-right: -3px;
      font-size: 6rem;
      font-weight: 600;
      color: var(--color-red);

      @media screen and (max-width: 25rem /* 400px */) {
        font-size: 4rem;
      }
    }
  }

  &__image {
    width: 7rem;
    height: 10rem;
    object-fit: cover;
    border-radius: 3.5rem;

    @media screen and (max-width: 25rem /* 400px */) {
      width: 5rem;
      height: 8rem;
    }

    @media screen and (max-width: 18.75rem /* 300px */) {
      width: 3rem;
      height: 6rem;
    }
  }

  &__author {
    position: absolute;
    left: 6.4rem;
    bottom: -3.8rem;
    display: flex;
    flex-direction: column;
    font-style: italic;
    font-size: 1.8rem;

    @media screen and (max-width: 25rem /* 400px */) {
      font-size: 1.5rem;
    }
  }
}

.swiper-slide {
  // filter: blur(2px) !important;
  transform: scale(0.75) !important;
  transition: all 250ms;

  &-active {
    filter: blur(0) !important;
    transform: scale(1) !important;
  }
}

.swiper-pagination-bullet {
  padding: 1rem;
  width: 3.5rem;
  height: 0.4rem;
  border-radius: 50vh;
  background: var(--color-white);
  box-shadow: inset 0 0 0 0.6rem var(--color-blue-dark-2);

  @media screen and (max-width: 25rem /* 400px */) {
    width: 3rem;
    height: 0.3rem;
  }

  &-active {
    width: 7rem;
    height: 0.7rem;

    @media screen and (max-width: 25rem /* 400px */) {
      width: 5rem;
      height: 0.5rem;
    }
  }
}
