.scroll-down {
  position: absolute;
  // bottom: 4.8rem;
  bottom: -100%;
  left: 50%;
  opacity: 0;
  transform: translate3d(-50%, 0, 0);

  &__wheel {
    height: 4.2rem;
    width: 2.4rem;
    border-radius: 10px;
    border: 2px solid var(--color-white);

    &--inner {
      position: absolute;
      left: 1rem;
      margin-top: 0.5rem;
      width: 0.4rem;
      height: 0.4rem;
      background: var(--color-white);
      border-radius: 50%;

      animation: scroll-wheel 0.5s linear infinite;
    }
  }

  &__arrows {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }

  &__arrow {
    width: 1.5rem;
    height: 1.5rem;
    border-right: 2px solid var(--color-white);
    border-bottom: 2px solid var(--color-white);
    transform: rotate(45deg);
    animation: scroll-arrow 1s linear infinite;

    @for $i from 1 through 3 {
      &:nth-child(#{$i}) {
        animation-delay: $i * 100ms;
      }
    }
  }
}
